@-webkit-keyframes modal-video-inner {
  from {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
    opacity: 0;
  }

  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes modal-video-inner {
  from {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
    opacity: 0;
  }

  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@-webkit-keyframes modal-video {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes modal-video {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
