.modal-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000000;
  cursor: pointer;
  opacity: 1;
  -webkit-animation: modal-video 0.3s ease-in;
          animation: modal-video 0.3s ease-in;
}

.modal-video-body {
  max-width: 800px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.modal-video-inner {
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
}

.modal-video-movie-wrap {
  width: 90%;
  height: 0;
  position: relative;
  padding-bottom: 56.25%;
  background-color: #2c3e50;
  border-radius: 1rem;
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
  -webkit-animation: modal-video-inner 0.3s 0.1s ease-in;
          animation: modal-video-inner 0.3s 0.1s ease-in;
  box-shadow: 0 1rem 1.5rem -0.5rem rgba(0,0,0,0.5);
}

.modal-video-movie-wrap iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 1rem;
}

.modal-video-close-btn {
  position: absolute;
  z-index: 2;
  top: -2.5rem;
  right: 1rem;
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  overflow: hidden;
  border: none;
  background: transparent
}

.modal-video-close-btn:before {
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg)
}

.modal-video-close-btn:after {
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
          transform: translate(-50%, -50%) rotate(-45deg)
}

.modal-video-close-btn:before,
.modal-video-close-btn:after {
  content: '';
  position: absolute;
  height: 3px;
  width: 100%;
  top: 50%;
  left: 50%;
  background: #fff;
  border-radius: 5px;
}
